import React from 'react';
import { Link, Route, Routes } from 'react-router-dom';
import FooterComponent from '../FooterComponent';
import HeaderComponent from '../HeaderComponent';

function SigninComponent(props) {

    return (
        <>
            <HeaderComponent />
            <div id='nomember'>
                <div className="container">
                    <div className="gap">
                        <div className="title">
                            <div className="title-box">
                                <h1> </h1>
                            </div>
                        </div>
                        <div className="content">
                            <div className="member-tab">
                                <ul>
                                    <li><label htmlFor="tab1" role='button'><Link to='/'> </Link></label></li>
                                    <li><label htmlFor="tab2" role='button'> </label></li>
                                </ul>
                            </div>
                            <form id="formLogin" method='post' >
                                <div className="member-login-box">
                                    <div className="login-input-box">
                                        <input type="text" name='userId' id='userId' placeholder='' />
                                        <input type="password" name='userPw' id='userPw' placeholder='' />
                                    </div>
                                </div>
                                <div className="login-chek-box">
                                    <div className="id-chek">
                                        <span className='form-id'>
                                            <img src="../img/icon_caution.png" alt="" />
                                            <label htmlFor="saveId" className='check-on'>   ,    .</label>
                                        </span>
                                    </div>
                                </div>
                                <div className="login-btn-box">
                                    <button type='submit' className='member-login-btn'></button>
                                </div>
                            </form>
                            <div className="member-sns-login">
                                <a href="!#">
                                    <img src="../img/pc_naver.png" alt="" />
                                    <p><br /></p>
                                </a>
                                <a className='kakao' href="!#">
                                    <img className='Kakao' src="../img/pc_kakao.png" alt="" />
                                    <p><br /></p>
                                </a>
                                <a href="!#">
                                    <img src="../img/pc_apple.png" alt="" />
                                    <p>Apple<br /></p>
                                </a>
                            </div>
                            <div className="signup-btn-box">
                                <span> +    <strong> 4 !</strong></span>
                                <button className='signup-btn'></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <FooterComponent />
        </>
    );
}

export default SigninComponent;